<script>
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import axios from 'axios';
import Utils from '../../../../../utils/Util'

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Log',
	},
	components: { Layout, PageHeader },
	data() {
		return {
			title: 'Log',
			api: Utils.Host(),
			message: "",
			data:[],
			options: "",
			alert:false,
			datatampil: [],
			modelpage:"1",
			modellimit: "10",
			datatampil: [],
			modelsearch: "",
		}
	},
	mounted() {
      console.log("alamat", Utils.Host())
      this.Getdata()
    },
	computed: {
	  pilihmodelpage() {
        return this.Getdata(this.modelpage);
      },
      pilihmodellimit() {
        return this.Getdata(this.modelpage, this.modellimit);
      },
	  pilihmodelsearch() {
        return this.Getdata(this.modelpage, this.modellimit, this.modelsearch);
      },
    },
	methods: {
      async Getdata(page,limit,search) {
        try {
			let kodepage = page ? page : 1;
			let kodelimit = limit ? limit : 10;
            let kodesearch = search ? search : "";
            let response = await axios.get(this.api+"/sertifikatlog?search="+kodesearch+
            "&limit="+
            kodelimit+
            "&page="+
            kodepage, Utils.authHeader());
            this.data = response.data.Data;
        } catch (err) {
            console.error(err);
        }
      },
	}
}
</script>

