										label="Halaman"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="modelpage"
										type="number"
										required
										@click="pilihmodelpage"
									></b-form-input>
									</b-form-group>
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Jumlah Tampil"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="modellimit"
										type="number"
										required
										@click="pilihmodellimit"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Data"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="modelsearch"
										type="text"
										required
										@click="pilihmodelsearch"
									></b-form-input>
									</b-form-group>
									<br>&nbsp;&nbsp;
									<br>
									<b-alert
											v-model="alert"
											variant="success"
											dismissible
											>{{ message }}</b-alert
										>
									<br>
									<div class="table-responsive">
									<table class="table mb-0">
										<thead>
										<tr>
											<th scope="col">No</th>
											<th scope="col">Data</th>
											<th scope="col">Action</th>
											<th scope="col">Table</th>
											<th scope="col">Username</th>
											<th scope="col">Tanggal Buat</th>
										</tr>
										</thead>
										<tbody>
										<tr v-for="(tampil,index) in data" :key="tampil.f_tgl_create">
											<th scope="row">{{index + 1}}</th>
											<td>{{tampil.f_data}}</td>
											<td>{{tampil.f_action}}</td>
											<td>{{tampil.f_table}}</td>
											<td>{{tampil.f_username}}</td>
											<td>{{tampil.f_tgl_create}}</td>
										</tr>
										</tbody>
									</table>
									</div>

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
